.card-wrapper {
    display: flex;
    flex-direction: column;
    width: 125px;
    border-radius: 20px;
    background-color: var(--white);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transition: transform 0.3s;
    cursor: pointer;
}

.card-wrapper:hover {
    transform: scale(1.05);
}

.image {
    padding: 0.5rem;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.name {
    padding: 0.5rem;
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
}