@font-face {
  font-family: marvel;
  src: url(assets/kung-fu-master-font/KungFuMaster3DItalic-MV7KP.otf)
}

:root {
  --gold: #d78e10;
  --blue: #4c75b7;
  --black: #000;
  --grey: #656a79;
  --white: #eee;
  --whiteHover: #ccc;
  --whiteActive: #aaa;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  word-wrap: break-word;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
  /* FOOTER */
  position: relative;
  min-height: 100%;
}

body {
  font-family: marvel, sans-serif;
  font-size: 1.6rem;
  background-color: var(--white);
  color: var(--black);
}

header {
  font-size: 3rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
}

a {
  text-decoration: none;
}

img {
  display: block;
  width: 100%;
}

button,
input,
textarea {
  border: none;
  outline: none;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
}

button {
  cursor: pointer;
}
